body{
    height: 100vh;    
    background-image: linear-gradient(to left, #C9D6FF, #2a5298);    
}

.card__{       
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);   
    padding: 0.75rem;
    background: rgba( 255, 255, 255, .45 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 4.5px );
    -webkit-backdrop-filter: blur( 4.5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    /* box-shadow:  0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);     */
}

.title{             
    background-color: #007bff;
    color: whitesmoke;
    font-size: 1.2rem;
    font-weight: 600;
    width: 260px;
    margin: 0 auto 0.5rem; 
    padding: 0.5rem .75rem;    
    text-align: center;
    border-radius: 5px 5px 0px 0px;
    border: none;    
}

.welcome-img{
    width: 260px;
    height: 90px;   
    margin: auto;
}

.login-img{
  width: 260px;
  height: 165px;   
  margin: auto;
}

.reset-img{
  width: 260px;
  height: 190px;   
  margin: auto;
}

.forgot-img{
  width: 260px;
  height: 240px;   
  margin: auto;
}

.real-img{
    width: 100%;
    height: 100%;     
}

input[type=text], [type=email], input[type=password]{
    border: none;
    background: rgba( 255, 255, 255, 0.30 );
}

.links{    
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
}

.link{
    text-decoration: none;
    padding-right: 0.5rem;    
}

.navbar-nav{
    margin-left: auto;
}

button{
    width: 100%;
}

.hidden{
    display: none;
}

.invalid, .valid, .unauthorized{
    width: 240px;
    font-size: 1.2rem;
    text-align: center;
}

@keyframes fadeInLeft {
    from {
      opacity: 0;
    }
  
    to {
      opacity: 1;
    }
  }
  @keyframes fadeOutLeft {
    from {
      opacity: 1;
    }
  
    to {
      opacity: 0;
    }
  }
  
  .fadeLeft-enter {
    animation-name: fadeInLeft;
    animation-duration: 400ms;
  }
  
  .fadeLeft-enter.fadeLeft-enter-active {
    animation-name: fadeInLeft;
    animation-duration: 400ms;
  }
  
  .fadeLeft-exit {
    animation-name: fadeOutLeft;
    animation-duration: 400ms;
  }
  
  .fadeLeft-exit.fadeLeft-exit-active {
    animation-name: fadeOutLeft;
    animation-duration: 400ms;
  }

